.input-group {
  display: flex;
  flex-wrap: wrap;
}

.input-group * {
  box-sizing: border-box;
}

.input-group label {
  flex: 1 1 100%;
}

.input-group input {
  flex: 1 1 100%;
  padding: 0.5em 0 0.5em 0.5em;
  border-radius: 0.3em;
  height: 2.5em;
}

.input-group .help-text {
  color: grey;
  display: block;
  font-size: 0.8em;
  font-style: italic;
}

.input-group ::-moz-placeholder {
  color: grey;
  font-size: 0.8em;
  font-style: italic;
}

.input-group ::-webkit-input-placeholder {
  color: grey;
  font-size: 0.8em;
  font-style: italic;
}

.input-group ::-ms-input-placeholder {
  color: grey;
  font-size: 0.8em;
  font-style: italic;
}

.input-group ::-ms-placeholder {
  color: grey;
  font-size: 0.8em;
  font-style: italic;
}

.input-radio-group {
}

.input-radio-group fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.input-radio-group .input-group {
  flex-wrap: nowrap;
  margin: 1em 0;
}

.input-radio-group .input-group input {
  order: -1;
  height: auto;
}

.input-radio-group .input-group input,
.input-radio-group .input-group label {
  flex: initial;
}

.input-radio-group .help-text {
  color: grey;
  display: block;
  font-size: 0.8em;
  font-style: italic;
}

.range-group {
  display: flex;
  flex-wrap: wrap;
}

.range-group * {
  box-sizing: border-box;
}

.range-group > div {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.range-group > div span{
  margin: 0 1em;
}

.range-group label {
  flex: 1 1 100%;
}

.range-group .help-text {
  color: grey;
  display: block;
  font-size: 0.8em;
  font-style: italic;
}

.range-group .label-min,
.range-group .label-max {
  text-align: center;
}

.range-group [type="range"] {
  box-sizing: border-box;
  -webkit-appearance: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 3em;
  background: repeating-linear-gradient(
    90deg,
    #ccc 0,
    #ccc 2px,
    transparent 0,
    transparent calc(100%/10 - 1.5px)
  ) 0.5em 2em no-repeat;
}

.range-group [type="range"]::-webkit-slider-runnable-track {
	border: none;
	width: 100%;
  height: 0.3em;
	background: #ccc;
}

.range-group [type="range"]::-moz-range-track {
	border: none;
	width: 100%;
  height: 0.3em;
	background: #ccc;
}

.range-group [type="range"]::-ms-track {
	border: none;
	width: 100%;
  height: 0.3em;
	background: #ccc;
}

.range-group input[type=range]:focus {
  outline: none;
}

.range-group input[type=range]::-webkit-slider-thumb {
	-webkit-appearance: none;
	width: 1.2em;
	height: 1.2em;
	border: solid 1px #999;
	border-radius: 50%;
	background: #f0f0f0;
  margin-top: -0.4em;
}

.range-group input[type=range]::-moz-range-thumb {
	-webkit-appearance: none;
	width: 1.2em;
	height: 1.2em;
	border: solid 1px #999;
	border-radius: 50%;
	background: #f0f0f0;
}

.range-group input[type=range]::-ms-thumb {
	-webkit-appearance: none;
	width: 1.2em;
	height: 1.2em;
	border: solid 1px #999;
	border-radius: 50%;
	background: #f0f0f0;
}

.range-group .count3 {
  background: repeating-linear-gradient(
    90deg,
    #ccc 0,
    #ccc 2px,
    transparent 0,
    transparent calc(100%/3 - 5px)
  ) 0.5em 2em no-repeat;
}

.select-group {
  display: flex;
  flex-wrap: wrap;
}

.select-group * {
  box-sizing: border-box;
}

.select-group label {
  flex: 1 1 100%;
}

.select-group select {
  flex: 1 1 100%;
  padding: 0.5em 0 0.5em 0.5em;
  border-radius: 0.3em;
  height: 2.5em;
  background: none;
}

.select-group .help-text {
  color: grey;
  display: block;
  font-size: 0.8em;
  font-style: italic;
}

form {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  justify-content: center;
}

form > div {
  box-sizing: border-box;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(100% - 2em*2);
  margin: 2em 2em;
}

@media (min-device-width: 768px) {
  form.two-columns > div {
    box-sizing: border-box;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(50% - 2em*2);
    margin: 2em 2em;
  }
}

.results-group output {
  font-weight: bold;
  margin-left: 1.2em;
}

.results-group {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  justify-content: center;
}

.results-group > * {
  box-sizing: border-box;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(50% - 2em*2);
  margin: 1em 0;
}

.results-group label {
  text-align: right;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-size: 16px;
}

#root {
  max-width: 1024px;
  margin: 0 auto;
}

header {
  min-height: 6em;
  padding: 2em;
  background-color: #2F8B6F;
  color: white;
  margin-bottom: 2em;
}

.title {
  font-size: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
  color: #D39C47;
}

.results {
  background-color: #FFDBA3;
  margin: 2em 0;
}

.outputs output {
  color: #840C26;
}

.results .helpText {
  padding: 2em 2em;
  font-size: 0.8em;
}

.input-group input,
.select-group select {
  border: none;
  border-radius: 0;
  border-bottom: 2px solid #085F44;
}

.input-group ::-moz-placeholder {
  color: #B57D26;
}
.input-group ::-webkit-input-placeholder {
  color: #B57D26;
}
.input-group ::-ms-input-placeholder {
  color: #B57D26;
}
.input-group ::-ms-placeholder {
  color: #B57D26;
}

.range-group [type="range"]::-webkit-slider-runnable-track {
  height: 2px;
  background: #085F44;
}
.range-group [type="range"]::-moz-range-track {
  height: 2px;
  background: #085F44;
}
.range-group [type="range"]::-ms-track {
  height: 2px;
  background: #085F44;
}

.range-group input[type=range]::-webkit-slider-thumb {
	border: solid 1px #085F44;
	background: #4CA288;
}
.range-group input[type=range]::-moz-range-thumb {
	border: solid 1px #085F44;
	background: #4CA288;
}
.range-group input[type=range]::-ms-thumb {
	border: solid 1px #085F44;
	background: #4CA288;
}

.input-group label ,
.select-group label,
.range-group label,
.input-radio-group legend {
  color: #840C26;
  font-weight: bold;
}

.input-radio-group label {
  color: black;
  font-weight: normal;
}

